<template>
  <div class="pagingbox" v-if="cnt > 0">
    <span id="pagingArea">
      <div style="overflow: hidden">
        <div class="dhx_pline">
          <div>
            <div
              class="dhx_page fprev"
              v-show="nowPage > 0"
              @click="first()"
            ></div>
            <div
              class="dhx_page prev"
              v-show="nowPage > 0"
              @click="prev()"
            ></div>
            <div
              class="dhx_page"
              :class="{ dhx_page_active: num === selectPage }"
              v-for="num in pageArr"
              :key="num"
              @click="goToPage(num)"
            >
              <div>
                {{ num + 1 }}
              </div>
            </div>
            <div
              class="dhx_page next"
              v-show="nowPage < totalPage - 1"
              @click="next()"
            ></div>
            <div
              class="dhx_page fnext"
              v-show="nowPage < totalPage - 1"
              @click="last()"
            ></div>
          </div>
        </div>
      </div>
    </span>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Pagination",
  props: ["cnt", "actionParams"],
  data() {
    return {
      nowPage: 0,
      start: 1,
      end: 1,
      pageArr: [],
      totalPage: 1,
    };
  },
  created() {
    this.setting();
  },
  computed: {
    ...mapState("main", ["pageSize", "selectPage"]),
  },
  mounted() {},
  methods: {
    setting() {
      this.totalPage =
        this.cnt % this.pageSize !== 0
          ? Math.floor(this.cnt / this.pageSize) + 1
          : Math.floor(this.cnt / this.pageSize);
      this.cnt / this.pageSize;
      this.nowPage = this.selectPage;
      this.pageArr = this.setNum();
    },
    setNum() {
      var n = this.nowPage % 10;
      this.start = this.nowPage - n;
      this.end =
        this.start + 10 > this.totalPage ? this.totalPage : this.start + 10;
      var arr = [];
      var length = this.end - this.start;
      for (var i = 0; i < length; i++) {
        arr[i] = this.start;
        this.start++;
      }
      return arr;
    },
    goToPage(p) {
      this.nowPage = p;
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: p });
      this.$store.dispatch(`main/getLists`, { ...this.actionParams });
      this.$store.commit("main/setSearchOptionBackup");
    },
    first() {
      this.nowPage = 0;
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: this.nowPage });
      this.$store.dispatch(`main/getLists`, { ...this.actionParams });
      this.$store.commit("main/setSearchOptionBackup");
    },
    prev() {
      if (this.nowPage < 10) {
        this.nowPage = 0;
      } else {
        this.nowPage = this.nowPage - 10;
      }
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: this.nowPage });
      this.$store.dispatch(`main/getLists`, { ...this.actionParams });
      this.$store.commit("main/setSearchOptionBackup");
    },
    next() {
      this.nowPage = this.nowPage + 10;
      if (this.nowPage > this.totalPage - 1) {
        this.nowPage = this.totalPage - 1;
      }
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: this.nowPage });
      this.$store.dispatch(`main/getLists`, { ...this.actionParams });
      this.$store.commit("main/setSearchOptionBackup");
    },
    last() {
      this.nowPage = this.totalPage - 1;
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: this.nowPage });
      this.$store.dispatch(`main/getLists`, { ...this.actionParams });
      this.$store.commit("main/setSearchOptionBackup");
    },
  },
  beforeDestroy() {
    this.$store.commit("main/SET_SELECTPAGE", { selectPage: 0 });
  },
  watch: {
    cnt(newData, oldData) {
      if (oldData !== newData) {
        this.setting();
      }
    },
    selectPage(newData, oldData) {
      if (oldData !== newData) {
        this.setting();
      }
    },
    nowPage(newData, oldData) {
      if (oldData !== newData) {
        this.pageArr = this.setNum();
      }
    },
    pageSize(newData, oldData) {
      if (oldData !== newData) {
        this.setting();
      }
    },
  },
};
</script>

