<template>
  <div class="sw-header">
    <div class="sw-inner">
      <h1 class="sw-logo" @click="refreshPage"></h1>
      <div class="sw-fl-right">
        <ul class="sw-gnb">
          <li>
            <span class="login-info">{{ currentTime }}</span>
          </li>
          <li><span class="refresh" @click="refreshPage"></span></li>
          <li><span class="user-info">관리자님</span></li>
        </ul>
      </div>
      <div class="sw-fl-left">
        <p class="doc-total-count">
          <em>{{ totalValue }}</em> 개의 문서가 있습니다.
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  name: "headerview",
  data() {
    return {
      currentTime: "",
    };
  },
  computed: {
    ...mapState("main", ["lists", "totalValue"]),
  },
  mounted() {
    this.updateTime();
  },
  created() {
    this.$store.dispatch("main/getLists");
  },
  methods: {
    updateTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, "0");
      const day = String(now.getDate()).padStart(2, "0");
      const hours = String(now.getHours()).padStart(2, "0");
      const minutes = String(now.getMinutes()).padStart(2, "0");
      const seconds = String(now.getSeconds()).padStart(2, "0");

      this.currentTime = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },
    refreshPage() {
      window.location.reload();
    },
  },
};
</script>
