<template>
  <div class="sw-container">
    <div class="sw-inner">
      <section class="sw-search-area">
        <div class="sw-search-box">
          <div class="select_box">
            <select
              @change="getSearchFilter($event.target.value)"
              v-model="state"
            >
              <option value="">전체</option>
              <option value="_subject">제목</option>
              <option value="_sformtitle">양식명</option>
              <option value="_author">상신자</option>
              <option value="_startdate">상신일</option>
              <option value="_scompletedate">완료일</option>
            </select>
          </div>
          <input
            type="text"
            v-model="searchText"
            :disabled="isDateOption"
            v-on:keyup.enter="searchData($event.target.value)"
          />
          <button type="submit" @click="searchData(searchText)">
            <span>검색</span>
          </button>
        </div>
        <div class="date_box" :class="{ active: isDateOption }">
          <template v-for="option in dateOptions">
            <span
              :key="option.id"
              @click="changeDateOption(option)"
              :class="{ active: nowOption == option.id }"
              >{{ option.name }}</span
            >
          </template>
          <div>
            <span>기간설정</span>
            <div>
              <date-picker
                type="date"
                input-class="custom datepicker"
                valueType="format"
                v-model="startDateValue"
                :clearable="false"
                :lang="'ko'"
              ></date-picker>

              <span>~</span>

              <date-picker
                type="date"
                input-class="custom datepicker"
                valueType="format"
                v-model="endDateValue"
                :clearable="false"
                :lang="'ko'"
              ></date-picker>
            </div>
          </div>
        </div>
      </section>
      <section class="sw-content sw-content-main">
        <div class="gridbox gridbox_dhx_terrace isModern">
          <div class="xhdr">
            <div style="display: none; position: absolute"></div>
            <table class="hdr">
              <colgroup>
                <col width="75px" />
                <col width="492px" />
                <col width="115px" />
                <col width="180px" />
                <col width="126px" />
                <col width="126px" />
                <col width="126px" />
              </colgroup>
              <tbody>
                <tr>
                  <td class="center">
                    <div class="hdrcell">No.</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">제목</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">첨부</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">양식명</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">상신자</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">상신일</div>
                  </td>
                  <td class="center">
                    <div class="hdrcell">완료일</div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="objbox">
            <table class="obj">
              <colgroup>
                <col width="75px" />
                <col width="492px" />
                <col width="115px" />
                <col width="180px" />
                <col width="126px" />
                <col width="126px" />
                <col width="126px" />
              </colgroup>
              <tbody>
                <tr v-if="totalValue === 0">
                  <td colspan="7">요청하신 리스트가 없습니다</td>
                </tr>
                <tr v-for="(data, index) in lists" :key="index">
                  <td>
                    {{ pageSize * (selectPage + 1) - (pageSize - (index + 1)) }}
                  </td>
                  <td class="title left" :title="data._subject">
                    <a
                      :href="getViewUrl(data._unid)"
                      onClick="window.open(this.href , '', 'width=' + popupWidth + ',height=' + popupHeight + ',left='+ popupX + ', top='+ popupY); return false;"
                      target="_blank"
                      >{{ data._subject }}</a
                    >
                  </td>
                  <!-- 첨부파일 있을경우 clip_area 클래스에 on을 붙여주세요 -->
                  <td class="clip_area" :class="{ on: data._attach == 'true' }">
                    <img src="../resources/images/clip_ic.png" alt="첨부파일" />
                  </td>
                  <td :title="Lang(data._sformtitle)">
                    {{ Lang(data._sformtitle) }}
                  </td>
                  <td :title="Lang(data._author)">
                    {{ Lang(data._author) }}
                  </td>
                  <td :title="formattedDate(data._startdate)">
                    {{ formattedDate(data._startdate) }}
                  </td>
                  <td :title="formattedDate(data._scompletedate)">
                    {{ formattedDate(data._scompletedate) }}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <pagination
          :cnt="totalValue"
          :actionParams="{
            keywordSearch: searchText,
            state: state,
            startDate: startDate,
            endDate: endDate,
          }"
        ></pagination>
      </section>
    </div>
  </div>
</template>
<script>
import Pagination from "@/components/Util/Pagination.vue";
import { mapState } from "vuex";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/locale/ko";
import "vue2-datepicker/index.css";

export default {
  components: { Pagination, DatePicker },
  name: "listview",
  data() {
    return {
      dateOptions: [
        {
          id: "oneMonth",
          name: "1개월",
        },
        {
          id: "threeMonth",
          name: "3개월",
        },
        {
          id: "sixMonth",
          name: "6개월",
        },
        {
          id: "oneYear",
          name: "1년",
        },
      ],
      nowOption: "oneMonth",
      searchText: "",
      state: "",
      startDate: "",
      endDate: "",
    };
  },
  computed: {
    ...mapState("main", ["lists", "totalValue", "pageSize", "selectPage"]),
    isDateOption() {
      return this.state == "_startdate" || this.state == "_scompletedate";
    },
    startDateValue: {
      get() {
        return this.startDate;
      },
      set(value) {
        this.nowOption = "";
        this.startDate = value;
      },
    },
    endDateValue: {
      get() {
        return this.endDate;
      },
      set(value) {
        this.nowOption = "";
        this.endDate = value;
      },
    },
  },
  created() {
    this.$store.dispatch("main/getLists", { state: this.state });

    // window.open(this.getViewUrl("asdf"), "", "width=795, height=976");
    // return false;
  },
  mounted() {},
  updated() {
    if (this.startDate > this.endDate) {
      this.endDate = this.startDate;
    }
  },
  methods: {
    getSearchFilter(state) {
      this.state = state;
      this.startDate = "";
      this.endDate = "";
      this.nowOption = "oneMonth";
      if (state === "_startdate" || state === "_scompletedate") {
        this.searchText = "";
      }
      if (this.isDateOption) {
        let currentDate = new Date(); // 현재 날짜
        let oneMonthAgo = new Date(); // 현재 날짜를 복사하여 사용
        oneMonthAgo.setMonth(oneMonthAgo.getMonth() - 1); // 한 달 전으로 설정
        this.startDate = oneMonthAgo.toISOString().slice(0, 10);
        this.endDate = currentDate.toISOString().slice(0, 10);
      } else {
        this.startDate = "";
        this.endDate = "";
      }
    },
    getViewUrl(data) {
      return this.$router.resolve({
        name: "detail",
        params: { id: data },
      }).href;
    },
    searchData(searchKeyword) {
      if (this.searchText.length === 1) {
        alert("2글자이상 입력해주세요.");
        searchKeyword.target.focus();
        return;
      }
      this.$store.commit("main/SET_SELECTPAGE", { selectPage: 0 });
      this.$store.dispatch("main/getLists", {
        keywordSearch: searchKeyword,
        state: this.state,
        startDate: this.startDate,
        endDate: this.endDate,
      });
    },
    // 재고자산 제각 품의서
    Lang(str) {
      try {
        let arr = str.split(","); // ["ko: 김재환", "en: kimjaehwan", "zh:金在煥"];
        // let fallbackTranslation = ""; // 대체 언어 코드value
        // let foundTranslation = false; // 대체 언어 번역을 찾았는지 여부

        const position = { ko: 0, en: 1, zh: 2 };
        const datas = [];
        for (let item in arr) {
          let itemArr = arr[item].split(":"); // itemArr = ["ko", "김재환"];
          let itemLang = itemArr[0]; // 항목의 언어 코드  / itemLang = "ko";
          let itemTranslation = itemArr[1].replace("，", ","); // 항목의 번역 값   itemTranslation = "김재환";
          datas[position[itemLang]] = itemTranslation;
          // if (itemLang === lang) {
          //   // 언어 코드와 일치하는 항목이 있는 경우
          //   return itemTranslation; // 해당 번역 값 반환
          // } else if (itemLang === "zh" || itemLang === "en") {
          //   // 대체 언어 코드인 zh 또는 en인 항목이 있는 경우
          //   fallbackTranslation = itemTranslation; // 대체 언어 번역 값 저장
          //   foundTranslation = true; // 대체 언어 번역을 찾았음을 표시
          // }
        }
        let tempName = "";
        for (let name of datas) {
          if (name) {
            tempName = name;
            break;
          }
        }
        return tempName;

        // if (foundTranslation) {
        //   // 대체 언어 번역을 찾은 경우
        //   return fallbackTranslation; // 대체 언어 번역 값을 반환
        // } else {
        //   return str;
        // }
      } catch (error) {
        if (typeof str === "undefined") {
          return "";
        }
        return str;
      }
    },
    formattedDate(date) {
      const year = date.substring(0, 4);
      const month = date.substring(4, 6);
      const day = date.substring(6, 8);

      return `${year}-${month}-${day}`;
    },
    changeDateOption(option) {
      this.nowOption = option.id;
      if (this.endDate !== "") {
        let selected = new Date(this.endDate);
        if (option.id == "oneMonth") {
          selected.setMonth(selected.getMonth() - 1);
        } else if (option.id == "threeMonth") {
          selected.setMonth(selected.getMonth() - 3);
        } else if (option.id == "sixMonth") {
          selected.setMonth(selected.getMonth() - 6);
        } else if (option.id == "oneYear") {
          selected.setFullYear(selected.getFullYear() - 1);
        }
        this.startDate = selected.toISOString().slice(0, 10);
      }
      return this.startDate;
    },
  },
  watch: {},
};
</script>
<style scoped >
.mx-datepicker {
  width: 125px;
}
</style>