export default {
    SET_LISTSDATA(state, { lists, totalValue }) {
        state.lists = lists;
        state.totalValue = totalValue;
    },
    SET_SELECTPAGE(state, { selectPage }) {
        state.selectPage = selectPage;
    },
    setSearchOptionBackup(state, data) {
        state.searchOptionBackup = data;
    },
}