<template>
  <div class="sw-view" v-if="lists[0]">
    <div class="btns_area">
      <button
        type="button"
        class="attach_btn"
        @click="isAttach"
        v-if="
          lists[0]['attachments'] &&
          Array.isArray(lists[0]['attachments']) &&
          lists[0]['attachments'].length > 0
        "
      >
        첨부파일
      </button>
      <button @click="closeModal">닫기</button>
      <div class="file_modal" :class="{ active: onAttach === true }">
        <h2 class="modal_tit">첨부파일</h2>
        <span class="close_btn" @click="isAttach"></span>
        <div class="modal_body">
          <ul class="file_list">
            <!-- {{
              lists[0].attachments
            }} -->
            <li
              v-for="(attachLists, index) in lists[0].attachments"
              :key="index"
            >
              <a href="#" class="file_name" :title="attachLists.originalName">
                <span
                  class="file_icon"
                  :class="getFileIconClass(attachLists.originalName)"
                ></span>
                {{ attachLists.originalName
                }}<em>{{ attachFileSize(attachLists.size) }}</em>
              </a>
              <span
                class="download_btn"
                @click="fileDownload(attachLists)"
              ></span>
            </li>
          </ul>
        </div>
        <div class="modal_btm">
          <button type="button" @click="allFileDownload(lists[0].attachments)">
            전체다운로드
          </button>
        </div>
      </div>
    </div>
    <div class="con_area">
      <h2 class="view_title" v-if="lists">{{ lists[0]._subject }}</h2>
      <div :class="iclass">
        <iframe ref="frame" :src="lists[0]._openurl" frameborder="0"></iframe>
        <!-- {{ "message" }}
        // 'message' 변수를 찾을 수 없음. => 'message' 변수안의 'Hello' 출력
        // "message" 문자열 출력 -->
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  name: "detail",
  data() {
    return {
      onAttach: false,
      iclass: "",
      exts: {
        hwp: ".hwp",
        psd: ".psd",
        gif: ".gif",
        doc: ".doc",
        js: ".js",
        htm: ".htm",
        txt: ".txt",
        png: ".png",
        css: ".css",
        pdf: ".pdf",
        xls: [".xls", ".xlsx"],
        zip: ".zip",
        jpg: ".jpg",
        ppt: [".ppt", ".pptx"],
      },
    };
  },
  computed: {
    ...mapState("main", ["lists"]),
    // sizeCalculation(attachLists) {
    //   let fileSize = attachLists.size;
    //   let dispFileSize = fileSize;
    //   let fileUnit = "Byte";
    //   if (Math.floor(fileSize / (1024 * 1024 * 1024)) > 0) {
    //     // GB
    //     fileUnit = "GB";
    //     dispFileSize = fileSize / (1024 * 1024 * 1024);
    //   } else if (Math.floor(fileSize / (1024 * 1024)) > 0) {
    //     // MB
    //     fileUnit = "MB";
    //     dispFileSize = fileSize / (1024 * 1024);
    //   } else if (Math.floor(fileSize / 1024) > 0) {
    //     // KB
    //     dispFileSize = fileSize / 1024;
    //     fileUnit = "KB";
    //   }
    //   return fileUnit, dispFileSize;
    // },
  },
  created() {
    this.$store.dispatch("main/getLists", {
      id: this.$route.params.id,
    });
  },
  // 투자사업 마감 건에 대한 자산단위표 변경 요청
  updated() {
    this.$nextTick(() => {
      const frame = this.$refs.frame;
      frame.onload = () => {
        if (this.lists[0]._openurl.endsWith(".pdf")) {
          // .pdf 파일인지 확인
          this.iclass = "pdf";
          const contentHeight = frame.contentWindow.document.body.scrollHeight;
          const contentWidth = frame.contentWindow.document.body.scrollWidth;
          const adjustedHeight = Math.max(contentHeight, 800) + "px";
          const adjustedWidth = Math.max(contentWidth, 800) + "px";

          frame.style.height = adjustedHeight;
          frame.style.width = adjustedWidth;

          const parentDiv = frame.parentNode;
          parentDiv.style.width = adjustedWidth;
          parentDiv.style.height = adjustedHeight;
        } else {
          this.iclass = "html";
          console.log(frame.contentWindow.document.body.scrollHeight);
          console.log(
            "frame.contentWindow.document.body.scrollWidth",
            frame.contentWindow.document.body.scrollWidth
          );
          console.log(frame.style.height);
          // frame.style.height = `${
          //   frame.contentWindow.document.body.scrollHeight + 50
          // }px`;
          // frame.style.width = `${
          //   // frame.contentWindow.document.body.scrollWidth + 20
          // }px`;
          frame.style.height = "100%";
          frame.style.width = "100%";
          console.log(frame.style.height);

          const parentDiv = frame.parentNode;
          parentDiv.style.width = `${
            frame.contentWindow.document.body.scrollWidth + 50
          }px`;
          // parentDiv.style.height = `${
          //   frame.contentWindow.document.body.scrollHeight + 20
          // }px`;
        }
      };
    });
  },
  mounted() {},
  methods: {
    closeModal() {
      window.close();
    },
    isAttach() {
      this.onAttach = !this.onAttach;
    },
    getFileIconClass(fileName) {
      let fileExt = fileName.substring(
        fileName.lastIndexOf("."),
        fileName.length
      );
      let classs = [];
      for (let ext in this.exts) {
        if (this.exts[ext].includes(fileExt)) {
          classs.push(ext);
          break;
        }
      }
      if (classs.length == 0) {
        classs.push("etc");
      }
      return classs;
    },
    fileDownload(file) {
      const link = document.createElement("a");
      link.href = file.url;
      link.target = "_blank";
      link.download = file.originalName;
      link.click();

      link.addEventListener("load", () => {
        link.remove();
      });
    },

    allFileDownload(allFile) {
      for (let file of allFile) {
        this.fileDownload(file);
      }
    },
    attachFileSize(size) {
      if (size.constructor == String) {
        size = Number(size);
      }
      let dispFileSize = size;
      let fileUnit = "Byte";

      if (Math.floor(size / (1024 * 1024 * 1024)) > 0) {
        // GB
        fileUnit = "GB";
        dispFileSize = size / (1024 * 1024 * 1024);
      } else if (Math.floor(size / (1024 * 1024)) > 0) {
        // MB
        fileUnit = "MB";
        dispFileSize = size / (1024 * 1024);
      } else if (Math.floor(size / 1024) > 0) {
        // MB
        fileUnit = "KB";
        dispFileSize = size / 1024;
      }

      return `(${dispFileSize.toFixed(2)}${fileUnit})`;
    },
  },
};
</script>
